// src/store.js
import { createStore } from 'vuex'

export default createStore({
  state: {
    modelList: ['Add Custom Model', 'ResNet50']

  },
  mutations: {
    ADD_MODEL(state, model) {
      if (!state.modelList.includes(model)) {
        state.modelList.push(model);
      }
    }
  }
});
