<template>
    <div :style="{ backgroundColor: backgroundColor }"></div>
    <div>
      <h1>Welcome to the MLOps Platform</h1>
      <p>
        Get started by choosing an option from the sidebar.
      </p>
      <UserGuide />
  
      <!-- Color pickers -->
      <div>
        <label>Background Color: </label>
        <input type="color" v-model="backgroundColor" />
      </div>
  
      <div>
        <label>Primary Text Color: </label>
        <input type="color" v-model="primaryTextColor" />
      </div>
  
      <div>
        <label>Secondary Text Color: </label>
        <input type="color" v-model="secondaryTextColor" />
      </div>
    </div>
  </template>
  
  <script>
  import UserGuide from '@/components/UserGuide.vue';
  
  export default {
    name: "HomePage",
    components: {
      UserGuide
    },
    data() {
      return {
        backgroundColor: "#222830",
        primaryTextColor: "#BC8824",
        secondaryTextColor: "#ffffff"
      };
    },
    watch: {
      backgroundColor(value) {
        document.documentElement.style.setProperty('--global-bg-color', value);
      },
      primaryTextColor(value) {
        document.documentElement.style.setProperty('--global-primary-text-color', value);
      },
      secondaryTextColor(value) {
        document.documentElement.style.setProperty('--global-secondary-text-color', value);
      }
    }
  };
  </script>
  
  <style scoped>
  h1 {
    margin-bottom: 30px;
      text-align: center
  }
  
  p {
    margin-bottom: 30px;
  }
  </style>
  