<template>
  <div>
    <!-- Dataset Upload Section -->
    <div class="section">
      <h2>Upload Dataset</h2>
      <input type="file" accept=".zip" ref="fileInput" @change="handleFileUpload" />
      <button class="upload-data" @click="uploadDataset">Upload</button>
      <p v-if="uploadMessage">{{ uploadMessage }}</p>
    </div>

    <!-- Model Configuration Section -->
    <div class="section">
        <h2>Model Configuration</h2>
        <label>Model Architecutre</label>
        <select v-model="selectedModel">
          <option disabled value="">Please select a model</option>
          <option v-for="model in modelList" :key="model" :value="model">{{ model }}</option>
        </select>

        <label for="transfer-learning-option" class="model-name" v-if="selectedModel === 'Add Custom Model'">Use ResNET50 As Base Model</label>
        <input class="model-name-input" type="text" v-model="customModelName" v-if="selectedModel === 'Add Custom Model'" placeholder="e.g. Custom 5 CNN">

        <label for="transferLearningOption" class="model-name" v-if="selectedModel === 'Add Custom Model'">Use ResNET50 As Base Model</label>
        <select class="transfer-learning" v-model="transferLearningOption" v-if="selectedModel === 'Add Custom Model'">
          <option disabled value="">Perform Transfer Learning</option>
          <option>No</option>
          <option>Yes</option>
        </select>
    </div>

    <div class="section">
      <button class="save-model-button" v-if="selectedModel === 'Add Custom Model'" @click="saveModel">Save Model</button>
        <p v-if="modelSavedMessage">{{ modelSavedMessage }}</p>

        <!-- Display textarea when 'Custom' is selected -->
        <textarea class="custom-model" v-if="selectedModel === 'Add Custom Model'" v-model="customModelJSON" placeholder="        Enter JSON of custom model each layer in a new line.
        TensorFlow Example
        Conv2D(filters=32, kernel_size=(3,3), strides=(2,2), padding='valid)
        MaxPooling2D(pool_size=(2, 2), strides=None, padding='valid')
        Flatten()
        Dense(units=2)
        
        PyTorch Example
        nn.Conv2d(in_channels=3, out_channels=32, kernel_size=(3,3), stride=1, padding=1)
        nn.MaxPool2d(kernel_size=(2, 2), stride=None)
        nn.Flatten()
        nn.Linear(in_features=512, out_features=2)
        
        Note: You don't need to explicity pass the attribute for each layer, just make sure that the parameter values correspond to the proper order depending
        on your framework of choice"></textarea>   
    </div>

    <!-- Training Configuration Section -->
    <div class="section">
      <h2>Training Configuration</h2>
      <label>Framework</label>
      <select class="selectFramework" v-model="framework">
          <option disabled value="">Please select a framework</option>
          <option v-for="framework in frameworks" :key="framework">{{ framework }}</option>
        </select>
        
      <label>Learning Rate</label>
      <input class="train-configs" type="se" v-model="learningRate" placeholder="Learning Rate" />
      <label>Epochs</label>
      <input class="train-configs" type="se" v-model="epochs" placeholder="Epochs" />
    </div>

    <div class="trainconfig2">
      <label>Optimizer</label>
      <select v-model="optimizer">  
        <option disabled value="">Please select a optimizer</option>
        <option>Adam</option>
        <option>RMSprop</option>
        <option>SGD</option>
        <option>Adagrad</option>
      </select>      

      <label class="trainconfig2-labels">Batch Size</label>
      <input class="train-configs" type="se" v-model="batchSize" placeholder="Batch Size" />

    </div>

    <div>
        <button class="train-button" @click="startTraining">Train Model</button>
    </div>

    <!-- Training Progress Bar -->
    <div class="section">
      <vue-progress-bar></vue-progress-bar> <!-- from 'vue-progressbar' package -->
    </div>

    <!-- Placeholder for TensorBoard -->
    <div class="section">
      <h2>Training Metrics</h2>
      <!-- This is a placeholder. Actual TensorBoard embedding will require backend setup. -->
      <div id="tensorboard-placeholder">TensorBoard will be embedded here.</div>
    </div>
  </div>
</template>

<script>
import { VueProgressBar } from 'vue-progressbar'

export default {
  name: 'ModelTraining',
  components: {
    VueProgressBar
  },
  computed: {
    modelList() {
      return this.$store.state.modelList;
    }
  },
  data() {
    return {
      selectedModel: '',
      customModelJSON: '',
      learningRate: 0.001,
      epochs: 10,
      batchSize: 32,
      optimizer: 'Adam',
      framework: '',
      frameworks: [ 'PyTorch' , 'TensorFlow'],
      modelName: '',
      transferLearning: '',
      modelSavedMessage: '',
      customModelName: '',
      uploadMessage: '',
      customModel: '',
    }
  },
  
  methods: 
    {
      handleFileUpload() 
      {
        // Handle the file upload here
        // You can send the file to the backend or read its contents as needed
      },

      async startTraining() 
      {
        try 
        {
          const response = await this.$axios.post('YOUR_API_GATEWAY_ENDPOINT', {
            framework: this.framework,
            optimizer: this.optimizer,
            learningRate: this.learningRate,
            batchSize: this.batchSize,
            epochs: this.epochs,
            // Add any other parameters you need
          });
        console.log('Training started:', response.data);
        } 
        catch (error) 
        {
          console.error('Error starting training:', error);
        }
      },

      async fetchTrainingProgress() 
      {
        try 
        {
          const response = await this.$axios.get('API_GATEWAY_URL_FOR_FETCHING_LOGS');
          console.log('Training progress:', response.data);
          // Update progress bar or display logs as needed
        } 
        catch (error) 
        {
          console.error('Error fetching training progress:', error);
        }
      },

      uploadDataset() 
      {
        const fileInput = this.$refs.fileInput;

        // Check if no file is selected
        if (!fileInput.files || fileInput.files.length === 0) 
        {
          this.uploadMessage = 'Please upload a dataset!';
          return; // Exit the function early
        }

        // Check if the selected file is not a zip file
        if (fileInput.files[0].type !== 'application/zip') 
        {
          this.uploadMessage = 'Please upload ZIP files only!';
          return; // Exit the function early
        }

        // Logic to upload the dataset to the storage
        // The storage will be connected to a cloud platform (AWS, Azure, GCP, IBM)
        // ... rest of the logic to upload dataset goes here ...

        // Show success message after the dataset is uploaded
        this.uploadMessage = `${fileInput.files[0].name} has been uploaded!`;

        // Optionally, you can reset the file input after the upload for better UX:
        // fileInput.value = '';
      },



      saveModel() {
      const modelName = this.customModelName;
      const transferLearning = this.transferLearningOption;
      const customModel = this.customModelJSON
      
      // Check if customModelName is empty
      if (modelName.trim() === '') {
        this.modelSavedMessage = 'Please enter a custom model name!';
        return;
      }

      if (transferLearning.trim() === '') {
        this.modelSavedMessage = 'Please select if you want to perform transfer learning or not!';
        return;
      }

      if (customModel.trim() === '') {
        this.modelSavedMessage = 'Please enter a model architecutre!';
        return;
      }
      
      if (!this.modelList.includes(modelName)) {
        this.$store.commit('ADD_MODEL', modelName);
        this.modelSavedMessage = `${modelName} saved successfully!`;
      } else {
        this.modelSavedMessage = `Model with name ${modelName} already exists!`;
      }
    },
    clearModelSavedMessage() {
      this.modelSavedMessage = '';
    }
  }
}
</script>

<style scoped>
.section {
  margin-bottom: 100px;
  margin-left: 30px;
}

.trainconfig1 {
  margin-bottom: 100px;
  margin-left: 30px;
}

.trainconfig2 {
  margin-bottom: 100px;
  margin-left: 30px;
}

label {
  font-weight: bold;
  margin-right: 20px;
}

.trainconfig2-labels {
  font-weight: bold;
  margin-right: 20px;
  margin-left: 50px;
}

.model-name {
    font-weight: bold;
    margin-left: 100px;
}

.transfer-learning {
  font-weight: bold;
  width:10%;
}


input {
    margin-right: 30px;
    color: var(--global-primary-text-color);
    font-weight: bold; 
}


.model-name-input {
    margin-right: 30px;
    color: var(--global-primary-text-color);
    border: 4px solid var(--global-primary-text-color);
    background-color: #ffffff;
    font-weight: bold; 
}

.train-configs {
    margin-right: 30px;
    color: var(--global-primary-text-color);
    border: 4px solid var(--global-primary-text-color);
    background-color: #ffffff;
    font-weight: bold; 
}

button.train-button {
    font-weight: bold;
    align-items: center;
    margin-left: auto;
    margin-right: auto;
    display:block;
    color: var(--global-secondary-text-color);
    border: 4px solid var(--global-primary-text-color);
    padding: 8px 8px;
}

button.upload-data {
    margin:0 auto;
    margin-left: 300px;
    color: var(--global-secondary-text-color);
    border: 4px solid var(--global-primary-text-color);
    padding: 8px 20px;
}

button.save-model-button {
    margin:0 auto;
    margin-left: 640px;
    color: var(--global-secondary-text-color);
    border: 4px solid var(--global-primary-text-color);
    padding: 8px 20px;
}

h2 {
    margin-bottom: 30px;
}

.custom-model {
  margin-top: 30px;
  width: 83%;
  height: 150px;
  padding: 12px 20px;
  box-sizing: border-box;
  border: 2px solid #ccc;
  border-radius: 4px;
  background-color: #f8f8f8;
  font-size: 16px;
  font-weight: bold;
  resize: none;
  border-color: var(--global-primary-text-color)
}

select {
  font-weight: bold;
  color: var(--global-primary-text-color);
  border: 2px solid var(--global-secondary-text-color);
}

.selectFramework {
  margin-right:30px;
}

</style>
